import React, { useEffect, useRef } from 'react';
import { FIRST_LEVEL } from '../resources/Map2';

const Platform = ({ canvasHeight, canvasWidth }) => {
  const canvasRef = useRef();
  const requestRef = useRef();

  const key = { left: false, right: false, up: false };
  const viewport = { x: 200, y: 200 };

  let tileSize = 16;
  let limitViewport = false;
  let jumpsSwitch = 0;
  let camera = { x: 0, y: 0 };
  let lastTile;
  let currentMap;

  const player = {
    loc: { x: 0, y: 0 },
    vel: { x: 0, y: 0 },
    canJump: true,
    colour: '#FF9900',
  };

  let touchedStartX;
  let touchedStartY;

  const handleStart = (eve) => {
    touchedStartX = eve.touches[0].clientX;
    touchedStartY = eve.touches[0].clientY;
  };

  const handleEnd = () => {
    key.left = false;
    key.up = false;
    key.right = false;
  };

  const handleMove = (eve) => {
    if (touchedStartY > eve.touches[0].clientY + 5) {
      key.up = true;
    }

    if (touchedStartX > eve.touches[0].clientX) {
      key.left = true;
    }

    if (touchedStartX < eve.touches[0].clientX) {
      key.right = true;
    }
  };

  const handleKeyDown = ({ keyCode }) => {
    switch (keyCode) {
      case 37:
        key.left = true;
        break;
      case 32:
      case 38:
        key.up = true;
        break;
      case 39:
        key.right = true;
        break;
      default:
        break;
    }
  };

  const handleKeyUp = ({ keyCode }) => {
    switch (keyCode) {
      case 37:
        key.left = false;
        break;
      case 32:
      case 38:
        key.up = false;
        break;
      case 39:
        key.right = false;
        break;
      default:
        break;
    }
  };

  const loadMap = (map) => {
    if (
      typeof map === 'undefined' ||
      typeof map.data === 'undefined' ||
      typeof map.keys === 'undefined'
    ) {
      console.error('Error: Invalid map data!');
      return false;
    }

    currentMap = map;
    currentMap.background = map.background || '#333';
    currentMap.gravity = map.gravity || { x: 0, y: 0.3 };
    currentMap.width = 0;
    currentMap.height = 0;

    tileSize = map.tileSize || 16;

    map.keys.forEach((key) => {
      map.data.forEach((row, y) => {
        currentMap.height = Math.max(currentMap.height, y);

        row.forEach((tile, x) => {
          currentMap.width = Math.max(currentMap.width, x);

          if (tile === key.id) {
            currentMap.data[y][x] = key;
          }
        });
      });
    });

    currentMap.widthP = currentMap.width * tileSize;
    currentMap.heightP = currentMap.height * tileSize;

    player.loc.x = map.player.x * tileSize || 0;
    player.loc.y = map.player.y * tileSize || 0;
    player.colour = map.player.colour || '#000';
    player.vel = {
      x: 0,
      y: 0,
    };

    camera = {
      x: 0,
      y: 0,
    };

    return true;
  };

  useEffect(() => {
    viewport.x = canvasWidth;
    viewport.y = canvasHeight;
    limitViewport = true;

    loadMap(FIRST_LEVEL);
  }, []);

  const getTile = (x, y) =>
    currentMap.data[y] && currentMap.data[y][x] ? currentMap.data[y][x] : 0;

  const movePlayer = () => {
    const tX = player.loc.x + player.vel.x;
    const tY = player.loc.y + player.vel.y;
    const offset = Math.round(tileSize / 2 - 1);

    const tile = getTile(
      Math.round(player.loc.x / tileSize),
      Math.round(player.loc.y / tileSize)
    );

    if (tile.gravity) {
      player.vel.x += tile.gravity.x;
      player.vel.y += tile.gravity.y;
    } else {
      player.vel.x += currentMap.gravity.x;
      player.vel.y += currentMap.gravity.y;
    }

    if (tile.friction) {
      player.vel.x *= tile.friction.x;
      player.vel.y *= tile.friction.y;
    }

    const t_y_up = Math.floor(tY / tileSize);
    const t_y_down = Math.ceil(tY / tileSize);
    const y_near1 = Math.round((player.loc.y - offset) / tileSize);
    const y_near2 = Math.round((player.loc.y + offset) / tileSize);
    const t_x_left = Math.floor(tX / tileSize);
    const t_x_right = Math.ceil(tX / tileSize);
    const x_near1 = Math.round((player.loc.x - offset) / tileSize);
    const x_near2 = Math.round((player.loc.x + offset) / tileSize);

    const top1 = getTile(x_near1, t_y_up);
    const top2 = getTile(x_near2, t_y_up);
    const bottom1 = getTile(x_near1, t_y_down);
    const bottom2 = getTile(x_near2, t_y_down);
    const left1 = getTile(t_x_left, y_near1);
    const left2 = getTile(t_x_left, y_near2);
    const right1 = getTile(t_x_right, y_near1);
    const right2 = getTile(t_x_right, y_near2);

    if (tile.jump && jumpsSwitch > 15) {
      player.canJump = true;
      jumpsSwitch = 0;
    } else {
      jumpsSwitch += 1;
    }

    player.vel.x = Math.min(
      Math.max(player.vel.x, -currentMap.velLimit.x),
      currentMap.velLimit.x
    );

    player.vel.y = Math.min(
      Math.max(player.vel.y, -currentMap.velLimit.y),
      currentMap.velLimit.y
    );

    player.loc.x += player.vel.x;
    player.loc.y += player.vel.y;
    player.vel.x *= 0.9;

    if (left1.solid || left2.solid || right1.solid || right2.solid) {
      // fix overlap
      while (
        getTile(Math.floor(player.loc.x / tileSize), y_near1).solid ||
        getTile(Math.floor(player.loc.x / tileSize), y_near2).solid
      ) {
        player.loc.x += 0.1;
      }

      while (
        getTile(Math.ceil(player.loc.x / tileSize), y_near1).solid ||
        getTile(Math.ceil(player.loc.x / tileSize), y_near2).solid
      ) {
        player.loc.x -= 0.1;
      }

      // tile bounce
      let bounce = 0;
      if (left1.solid && left1.bounce > bounce) {
        bounce = left1.bounce;
      }

      if (left2.solid && left2.bounce > bounce) {
        bounce = left2.bounce;
      }

      if (right1.solid && right1.bounce > bounce) {
        bounce = right1.bounce;
      }

      if (right2.solid && right2.bounce > bounce) {
        bounce = right2.bounce;
      }

      player.vel.x *= -bounce || 0;
    }

    if (top1.solid || top2.solid || bottom1.solid || bottom2.solid) {
      // fix overlap
      while (
        getTile(x_near1, Math.floor(player.loc.y / tileSize)).solid ||
        getTile(x_near2, Math.floor(player.loc.y / tileSize)).solid
      ) {
        player.loc.y += 0.1;
      }

      while (
        getTile(x_near1, Math.ceil(player.loc.y / tileSize)).solid ||
        getTile(x_near2, Math.ceil(player.loc.y / tileSize)).solid
      ) {
        player.loc.y -= 0.1;
      }

      // tile bounce
      let bounce = 0;

      if (top1.solid && top1.bounce > bounce) {
        bounce = top1.bounce;
      }

      if (top2.solid && top2.bounce > bounce) {
        bounce = top2.bounce;
      }

      if (bottom1.solid && bottom1.bounce > bounce) {
        bounce = bottom1.bounce;
      }

      if (bottom2.solid && bottom2.bounce > bounce) {
        bounce = bottom2.bounce;
      }

      player.vel.y *= -bounce || 0;

      if ((bottom1.solid || bottom2.solid) && !tile.jump) {
        player.onFloor = true;
        player.canJump = true;
      }
    }

    // adjust camera
    const c_x = Math.round(player.loc.x - viewport.x / 2);
    const c_y = Math.round(player.loc.y - viewport.y / 2);

    const x_dif = Math.abs(c_x - camera.x);
    const y_dif = Math.abs(c_y - camera.y);

    if (x_dif > 5) {
      const mag = Math.round(Math.max(1, x_dif * 0.1));

      if (c_x !== camera.x) {
        camera.x += c_x > camera.x ? mag : -mag;

        if (limitViewport) {
          camera.x = Math.min(
            currentMap.widthP - viewport.x + tileSize,
            camera.x
          );

          camera.x = Math.max(0, camera.x);
        }
      }
    }

    if (y_dif > 5) {
      const mag = Math.round(Math.max(1, y_dif * 0.1));

      if (c_y !== camera.y) {
        camera.y += c_y > camera.y ? mag : -mag;

        if (limitViewport) {
          camera.y = Math.min(
            currentMap.heightP - viewport.y + tileSize,
            camera.y
          );
          camera.y = Math.max(0, camera.y);
        }
      }
    }

    if (lastTile !== tile.id && tile.script) {
      switch (tile.script) {
        case 'death':
          // this.setState({ notification: 'You died! Try again ;)' });
          console.error('You died! Try again.');
          loadMap(FIRST_LEVEL);
          break;

        case 'change_colour':
          // player.colour = '#' + ((Math.random() * 0xffffff) << 0).toString(16);
          currentMap.keys[12].solid = 0;
          currentMap.keys[12].colour = '#888';
          player.colour = '#FF0000';
          break;

        case 'unlock':
          currentMap.keys[10].solid = 0;
          currentMap.keys[10].colour = '#888';
          break;

        case 'next_level':
          console.warn('Yay! You won! Reloading map.');
          loadMap(FIRST_LEVEL);
          break;

        default:
          console.error(currentMap.scripts[tile.script]);
          break;
      }
    }

    lastTile = tile.id;
  };

  const updatePlayer = () => {
    if (key.left) {
      if (player.vel.x > -currentMap.velLimit.x) {
        player.vel.x -= currentMap.movementSpeed.left;
      }
    }

    if (key.up) {
      if (player.canJump && player.vel.y > -currentMap.velLimit.y) {
        player.vel.y -= currentMap.movementSpeed.jump;
        player.canJump = false;
      }
    }

    if (key.right) {
      if (player.vel.x < currentMap.velLimit.x) {
        player.vel.x += currentMap.movementSpeed.left;
      }
    }

    movePlayer();
  };

  const drawTile = (x, y, tile, context) => {
    if (tile?.colour) {
      context.fillStyle = tile.colour;
      context.fillRect(x, y, tileSize, tileSize);
    }
  };

  const drawMap = (context, fore) => {
    for (let y = 0; y < currentMap.data.length; y += 1) {
      for (let x = 0; x < currentMap.data[y].length; x += 1) {
        if (
          (!fore && !currentMap.data[y][x].fore) ||
          (fore && currentMap.data[y][x].fore)
        ) {
          const t_x = x * tileSize - camera.x;
          const t_y = y * tileSize - camera.y;

          if (
            !(
              t_x < -tileSize ||
              t_y < -tileSize ||
              t_x > viewport.x ||
              t_y > viewport.y
            )
          ) {
            drawTile(t_x, t_y, currentMap.data[y][x], context);
          }
        }
      }
    }

    if (!fore) {
      drawMap(context, true);
    }
  };

  const drawPlayer = (context) => {
    context.fillStyle = player.colour;
    context.beginPath();

    context.arc(
      player.loc.x + tileSize / 2 - camera.x,
      player.loc.y + tileSize / 2 - camera.y,
      tileSize / 2 - 1,
      0,
      Math.PI * 2
    );

    context.fill();
  };

  const draw = (context) => {
    drawMap(context, false);
    drawPlayer(context);
  };

  const paint = () => {
    const ctx = canvasRef.current.getContext('2d');

    ctx.fillStyle = '#333';
    ctx.fillRect(0, 0, canvasWidth, canvasHeight);

    updatePlayer();
    draw(ctx);
  };

  const loop = () => {
    paint();
    requestRef.current = window.requestAnimationFrame(loop);
  };

  useEffect(() => {
    document.addEventListener('keydown', handleKeyDown);
    document.addEventListener('keyup', handleKeyUp);

    if ('ontouchstart' in window || navigator.msMaxTouchPoints) {
      document.addEventListener('touchstart', handleStart);
      document.addEventListener('touchend', handleEnd);
      document.addEventListener('touchmove', handleMove);
    }

    requestRef.current = window.requestAnimationFrame(loop);

    return () => {
      cancelAnimationFrame(requestRef.current);

      document.removeEventListener('keydown', handleKeyDown);
      document.removeEventListener('keyup', handleKeyUp);

      if ('ontouchstart' in window || navigator.msMaxTouchPoints) {
        document.removeEventListener('touchstart', handleStart);
        document.removeEventListener('touchend', handleEnd);
        document.removeEventListener('touchmove', handleMove);
      }
    };
  });

  return (
    <canvas
      data-testid="main-canvas"
      ref={canvasRef}
      height={canvasHeight}
      width={canvasWidth}
      style={
        canvasWidth >= 800
          ? {
              display: 'block',
              margin: '7rem auto',
            }
          : null
      }
    />
  );
};

export default Platform;
